// components/FrappeGanttFull.tsx
'use client';
import React, { useEffect, useRef, useState } from 'react';
import Gantt from 'frappe-gantt';
import 'frappe-gantt.css';
import apiClient from '../lib/api-client';

type WO = {
  id: string;
  doc_no?: string;
  asset_name?: string;
  work_type?: string;
  start_date?: string | null;
  end_date?: string | null;
  description?: string | null;
  raw?: any;
};

// pick color by work_type fallback
function pickColorForWork(w: WO) {
  const wt = (w.work_type || (w.raw && (w.raw.work_type || w.raw.type_work)) || '').toString().toLowerCase();
  if (wt.includes('preventive') || wt === 'pm') return '#16a34a';
  if (wt.includes('corrective') || wt === 'cm') return '#f97316';
  if (wt.includes('breakdown') || wt.includes('failure')) return '#ef4444';
  return '#3b82f6';
}

// format for frappe (YYYY-MM-DD HH:mm)
function toLocalDateStr(iso?: string|null) {
  if (!iso) return null;
  const d = new Date(iso);
  if (isNaN(d.getTime())) return null;
  const yyyy = d.getFullYear();
  const mm = String(d.getMonth() + 1).padStart(2, '0');
  const dd = String(d.getDate()).padStart(2, '0');
  const hh = String(d.getHours()).padStart(2, '0');
  const min = String(d.getMinutes()).padStart(2, '0');
  return `${yyyy}-${mm}-${dd} ${hh}:${min}`;
}

export default function FrappeGanttFull({ pageSize = 2000 }: { pageSize?: number }) {
  const containerRef = useRef<HTMLDivElement | null>(null);
  const ganttRef = useRef<any>(null);
  const [items, setItems] = useState<WO[]>([]);
  const [selectedDate, setSelectedDate] = useState<string>(new Date().toISOString().slice(0,10));
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string|null>(null);
  const [selected, setSelected] = useState<WO|null>(null);
  const [nowLeft, setNowLeft] = useState<number|null>(null);

  useEffect(() => { load(); }, []);

  async function load() {
    setLoading(true); setError(null);
    try {
      const res = await apiClient(`/work-orders?q=&page=1&pageSize=${pageSize}`);
      const rows = res?.data ?? res;
      const mapped: WO[] = (rows || []).map((r: any) => ({
        ...r,
        start_date: r.start_date ?? null,
        end_date: r.end_date ?? null,
        description: r.description ?? r.raw?.description ?? ''
      }));
      setItems(mapped);
    } catch (err: any) {
      console.error('load gantt', err);
      setError(err?.body?.message || err?.message || 'Gagal memuat data');
    } finally { setLoading(false); }
  }

  // build tasks filtered by selectedDate (only tasks that intersect the day)
  function buildTasksForDay(dateStr: string) {
    const dayStart = new Date(`${dateStr}T00:00:00`).getTime();
    const dayEnd = dayStart + 24*60*60*1000;

    const tasks = items
      .map(it => {
        const s = it.start_date ? Date.parse(it.start_date) : null;
        const e = it.end_date ? Date.parse(it.end_date) : null;
        // if no times skip
        if (!s && !e) return null;
        const start = s ?? e!;
        const end = e ?? s!;
        // intersects day?
        if (end <= dayStart || start >= dayEnd) return null;
        // clamp to day for better visual
        const sClamped = new Date(Math.max(start, dayStart));
        const eClamped = new Date(Math.min(end, dayEnd));
        return {
          id: it.id,
          name: `${it.doc_no ?? it.id} — ${it.asset_name ?? ''}`,
          start: toLocalDateStr(sClamped.toISOString()),
          end: toLocalDateStr(eClamped.toISOString()),
          progress: 0,
          data: it
        };
      })
      .filter(Boolean) as any[];

    // sort by raw start (ascending) so rows ordered earliest first
    tasks.sort((a,b) => {
      const sa = Date.parse(a.start);
      const sb = Date.parse(b.start);
      return sa - sb;
    });

    return tasks;
  }

  // create / refresh gantt instance
  useEffect(() => {
    if (!containerRef.current) return;
    // clear existing
    containerRef.current.innerHTML = '';

    const tasks = buildTasksForDay(selectedDate);
    if (tasks.length === 0) {
      // nothing to render
      setNowLeft(null);
      return;
    }

    // create gantt
    try {
      const gantt = new Gantt(containerRef.current, tasks, {
        view_mode: 'Day',
        date_format: 'YYYY-MM-DD HH:mm',
        on_click: (task: any) => {
          setSelected(task.data || null);
        },
        on_date_change: (task: any, start: any, end: any) => {
          // optional: implement patch to server
          console.log('date change', task, start, end);
        },
        on_view_change: (mode:any) => { /* ignore */ }
      });
      ganttRef.current = gantt;

      // color bars after a tick (DOM created)
      setTimeout(() => {
        try {
          tasks.forEach((t:any) => {
            const el = containerRef.current!.querySelector(`[data-id="${t.id}"] .bar`);
            if (el) {
              const color = pickColorForWork(t.data);
              (el as HTMLElement).style.fill = color;
              (el as HTMLElement).style.stroke = color;
            }
          });

          // compute now overlay X:
          computeNowOverlay(selectedDate);
          // recompute on window resize
          window.addEventListener('resize', onResizeComputeNow);
        } catch (e) { /* ignore */ }
      }, 120);
    } catch (e) {
      console.error('frappe create error', e);
    }

    return () => {
      window.removeEventListener('resize', onResizeComputeNow);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [items, selectedDate]);

  function onResizeComputeNow() {
    computeNowOverlay(selectedDate);
  }

  // compute left offset for "now" line (in pixels relative to containerRef)
  function computeNowOverlay(dateStr: string) {
    if (!containerRef.current) return setNowLeft(null);
    // try to find labels column width (selectors vary by version)
    const labelsEl = containerRef.current.querySelector('.labels, .gantt-labels, .grid .labels, .labels-wrap') as HTMLElement | null;
    const svgEl = containerRef.current.querySelector('svg') as SVGElement | null;

    // fallback: measure first label element width by querying first .labels element
    let leftOffset = 0;
    if (labelsEl) leftOffset = labelsEl.clientWidth;
    else {
      // find leftmost element child that seems label column (heuristic)
      const firstChild = containerRef.current.firstElementChild as HTMLElement | null;
      if (firstChild) leftOffset = firstChild.clientWidth * 0.6 || 200;
      else leftOffset = 200;
    }

    // timeline width: total svg width minus leftOffset (approx)
    let timelineWidth = svgEl ? svgEl.clientWidth - leftOffset : (containerRef.current.clientWidth - leftOffset);
    if (!timelineWidth || timelineWidth <= 0) timelineWidth = Math.max(400, containerRef.current.clientWidth - leftOffset - 20);

    // compute day start and totalMs
    const dayStart = new Date(`${dateStr}T00:00:00`).getTime();
    const dayEnd = dayStart + 24*60*60*1000;
    const nowMs = Date.now();
    if (nowMs < dayStart || nowMs > dayEnd) {
      setNowLeft(null);
      return;
    }
    const ratio = (nowMs - dayStart) / (dayEnd - dayStart);
    const x = leftOffset + Math.round(ratio * timelineWidth);
    // adjust container left (containerRef's bounding)
    // containerRef is the element that contains the svg; we want left relative to containerRef
    setNowLeft(x);
  }

  // change date handler
  function onDateChange(e: React.ChangeEvent<HTMLInputElement>) {
    setSelectedDate(e.target.value);
    // clear now overlay while new render happens
    setNowLeft(null);
  }

  return (
    <div style={{ padding: 12 }}>
      <div style={{ display:'flex', gap:8, alignItems:'center', marginBottom:12 }}>
        <label>
          Tanggal:
          <input type="date" value={selectedDate} onChange={onDateChange} style={{ marginLeft:8, padding:'6px 8px' }} />
        </label>
        <button onClick={() => { load(); }} disabled={loading}>Refresh</button>
        <div style={{ marginLeft:'auto', color:'#666' }}>Showing tasks for {selectedDate}</div>
      </div>

      {loading && <div>Loading...</div>}
      {error && <div style={{ color:'red' }}>{error}</div>}

      <div style={{ position:'relative', border:'1px solid #eee', borderRadius:6, overflowX:'auto' }}>
        {/* containerRef is where frappe mounts */}
        <div ref={containerRef}></div>

        {/* Now indicator: absolute positioned line */}
        {nowLeft !== null && (
          <div style={{
            position:'absolute',
            top: 30,
            left: nowLeft,
            height: Math.max(200, (containerRef.current?.clientHeight ?? 400) - 30),
            borderLeft: '2px dashed #ef4444',
            pointerEvents: 'none',
            zIndex: 20
          }} />
        )}
      </div>

      {/* modal detail */}
      {selected && (
        <div style={{
          position:'fixed', left:0, right:0, top:0, bottom:0, background:'rgba(0,0,0,0.35)',
          display:'flex', alignItems:'center', justifyContent:'center'
        }}>
          <div style={{ background:'white', width:720, maxHeight:'80%', overflowY:'auto', padding:16, borderRadius:8 }}>
            <h3>{selected.doc_no ?? selected.description}</h3>
            <div><strong>Asset:</strong> {selected.asset_name ?? selected.data?.asset_name}</div>
            <div><strong>Type:</strong> {selected.work_type ?? selected.data?.work_type}</div>
            <div><strong>Start:</strong> {selected.start_date ?? selected.data?.start_date}</div>
            <div><strong>End:</strong> {selected.end_date ?? selected.data?.end_date}</div>
            <div style={{ marginTop:12 }}>
              <pre style={{ whiteSpace:'pre-wrap', background:'#f7f7f7', padding:8 }}>{JSON.stringify(selected.raw ?? selected.data ?? selected, null, 2)}</pre>
            </div>
            <div style={{ textAlign:'right', marginTop:8 }}>
              <button onClick={() => setSelected(null)}>Close</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
